// import { ref } from "vue";
import { onMounted, watch } from "vue";

export function lazySetter(ref, delay = 200) {
  let timeout;
  let _delay = delay;
  const lazySet = (v, delay) => {
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => (ref.value = v), delay || _delay);
  };
  const setNow = (v) => {
    if (timeout) clearTimeout(timeout);
    ref.value = v;
  };
  return {
    lazy: lazySet,
    now: setNow,
  };
}

export function lazyWatch(source, act, delay = 200) {
  let timeout;
  const func = (v) => {
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => act(v), delay);
  };
  watch(source, func, { immediate: true });
}
export function watchOnMounted(source, act) {
  onMounted(() => {
    watch(source, act, { immediate: true });
  });
}
export function lazyWatchOnMounted(source, act, { delay = 200 } = {}) {
  onMounted(() => {
    act();
    lazyWatch(source, act, delay);
  });
}

/**
 *
 * @param obj
 * @param props {String|Object}
 * @param act {Function}
 * @param immediate {Boolean}
 */
export function watchProps(obj, props, act, immediate = false) {
  if (typeof props === "string") {
    watch(() => obj[props], act, { immediate });
  } else {
    const watches = [];
    for (let i = 0; i < props.length; i++) {
      watches.push(() => obj[props[i]]);
    }
    watch(watches, act, { immediate });
  }
}
