<template>
  <div class="vimeo-bg-container" style="width: 100%; height: 100%">
    <iframe
      ref="iframe"
      :src="`https://player.vimeo.com/video/${id}?api=1&background=1&loop=1&quality=${quality}`"
      allowfullscreen="allowfullscreen"
      frameborder="0"
    ></iframe>
  </div>
</template>

<script>
import { onMounted } from "vue";
import Player from "@vimeo/player";
import { templateRef } from "@vueuse/core";
import { watchProps } from "../../utils/reactiveHelpers";
export default {
  name: "HiResponsiveVimeo",
  props: {
    id: String,
    play: {
      type: Boolean,
      default: true,
    },
    quality: {
      type: String,
      default: "auto",
    },
  },
  setup(props, { emit }) {
    onMounted(() => {
      const iframe = templateRef("iframe");
      const player = new Player(iframe.value);

      player.on("play", function () {
        emit("play");
      });
      watchProps(
        props,
        "play",
        (v) => {
          if (v) player.play();
          else player.pause();
        },
        true
      );
    });
  },
};
</script>

<style scoped>
.vimeo-bg-container {
  padding: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
iframe {
  box-sizing: border-box;
  width: 177.77777778vh;
  height: 56.25vw;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
